import { createGlobalStyle } from 'styled-components';
import { darken } from 'polished';

export const GlobalStyle = createGlobalStyle`
  @media print {
    body {
      color: black !important;
      background: white !important;
    }
    
    a {
      color: black !important;
      text-decoration: none !important;
    }
  }

  @media screen {
    body {
      background: ${(props) => props.theme.light.colors.background} !important;
      color: ${(props) => props.theme.light.colors.textColor} !important;
      font-family: ${(props) => props.theme.fonts.sansSerif} !important;
    }

    h1, h2, h3, h4, h5, h6 {
      font-family: ${(props) => props.theme.fonts.serif} !important;
      font-weight: bold !important;
    }

    a {
      color: ${(props) => props.theme.light.colors.primary} !important;
      text-decoration: none !important;
    }

    a:hover {
      color: ${(props) => darken(0.1, props.theme.light.colors.primary)} !important;
    }

    .linear-bg {
      color: ${(props) => props.theme.light.colors.textAccent} !important;
      background: linear-gradient(
        45deg,
        ${(props) => darken(0.3, props.theme.light.colors.primaryAccent)},
        ${(props) => props.theme.light.colors.primaryAccent}
      );

      a {
        color: white !important;
      }

      a:hover {
        color: ${(props) => props.theme.light.colors.primary} !important;
      }
    }

    @media (prefers-color-scheme: dark) {
      body {
        background: ${(props) => props.theme.dark.colors.background} !important;
        color: ${(props) => props.theme.dark.colors.textColor} !important;
        font-family: ${(props) => props.theme.fonts.sansSerif} !important;
      }

      a {
        color: ${(props) => props.theme.dark.colors.primary} !important;
      }

      a:hover {
        color: ${(props) => darken(0.1, props.theme.dark.colors.primary)} !important;
      }

      .linear-bg {
        color: ${(props) => props.theme.dark.colors.textAccent} !important;
        background: linear-gradient(
          45deg,
          ${(props) => darken(0.3, props.theme.dark.colors.primaryAccent)},
          ${(props) => props.theme.dark.colors.primaryAccent}
        );

        a {
          color: white !important;
        }

        a:hover {
          color: ${(props) => props.theme.dark.colors.primary} !important;
        }
      }
    }
  }
`;
